import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { ProfileComponent } from './profile.component';

const routes: Routes = [
  {
    path: '',
    component: ProfileComponent,
    children:[
      {
        path: 'customers', loadChildren: () => import('../profile-data/customers/customers.module').then(m => m.CustomersModule)
      },
      {
        path: 'calendar', loadChildren: () => import('../profile-data/calendar/calendar.module').then(m => m.CalendarModule)
      },
      {
        path: 'logs', loadChildren: () => import('../profile-data/logs/logs.module').then(m => m.LogsModule)
      },
    ]
  },


];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ProfileRoutingModule { }
