import { Component, OnInit } from '@angular/core';
import { AuthService } from "../services/auth.service";
import {menu} from "./profile.menu";
import {CallerService} from "../services/caller.service";
import { CookieService } from "ngx-cookie-service";
import { FirestoreService } from "../services/firestore.service";

@Component({
  selector: 'app-profile',
  templateUrl: './profile.component.html',
  styleUrls: ['./profile.component.scss']
})
export class ProfileComponent implements OnInit {

public user:any;
public menu:any[]=menu;
private user_id=this.cookie.get("user_id");
  constructor(
    private db:FirestoreService,
    private cookie:CookieService,
    private auth:AuthService,
    private caller:CallerService
  ) { }

  ngOnInit(): void {
    this.getUserInfo();
  }

  signOut():void{
    this.auth.signOut();
  }
  addProfilePic():void{
    this.caller.trigger();
  }
  getUserInfo():void{
    const db=this.db.getDataById("users",this.user_id);
    db.subscribe((response:any)=>{
      if(response.hasOwnProperty("picture"))
      {
        this.user=response;
      }
      else
      {
        this.user=response;
        this.user["picture"]="https://iconape.com/wp-content/png_logo_vector/avatar-4.png";
      }
    });
  }

}
